        h6.mb-2 Factura electrnica
        br
        p Para la figura de facturacin electrnica debe considerarse el Estatuto Tributario (Secretara del Senado, 2020. Art. 617) y el Decreto 2242 (Sistema nico de Informacin Normativa, 2020):  
        br
        p Segn el Decreto 2242 (Sistema nico de Informacin Normativa, 2020. Art. 2) la factura electrnica: es el documento que soporta transacciones de venta de bienes y/o servicios y que operativamente tienen lugar a travs de sistemas computacionales y/o soluciones informticas que permiten el cumplimiento de las caractersticas y condiciones que se establecen en el presente Decreto, en relacin con la expedicin, recibo, rechazo y conservacin. La expedicin de la factura electrnica comprende la generacin por el obligado a facturar y su entrega al adquirente. 
      .col-auto 
        img.img-a.my-3(src="@/assets/curso/images/tema1/img_4.svg" alt="", style="width:300px; display:inline-block; margin:auto 5rem;").img-fluid

    p.mt-3 <b>Obligado a facturar electrnicamente:</b> persona natural o jurdica comprendida en el mbito de este Decreto y que como tal debe facturar electrnicamente en las condiciones que se establecen en los artculos siguientes. 
    br
    p <b>Adquirente:</b> persona natural o jurdica que adquiere bienes y/o servicios y debe exigir factura o documento equivalente y, que, tratndose de la factura electrnica, la recibe, rechaza, cuando sea del caso, y conserva para su posterior exhibicin, en las condiciones que se establecen en el presente decreto. 
    br
    p <b>Proveedor tecnolgico:</b> es la persona natural o jurdica que podr prestar a los obligados a facturar electrnicamente y/o a los adquirentes que opten por recibir la factura en formato electrnico de generacin, cuando unos u otros as lo autoricen, los servicios inherentes a la expedicin de la factura electrnica, incluida la entrega del ejemplar a la DIAN como se indica en el artculo 7 del presente Decreto, as como los servicios relacionados con su recibo, rechazo y conservacin. El proveedor tecnolgico deber surtir el proceso de autorizacin por parte de la DIAN previsto en el artculo 12 de este decreto. 
    br
    p <b>Catlogo de Participantes de Factura Electrnica:</b> es el registro electrnico administrado por la DIAN, que provee informacin de los obligados a facturar electrnicamente dentro del mbito del presente Decreto, de los adquirentes que decidan recibirla electrnicamente y proveedores tecnolgicos, con el fin de facilitar su operatividad. 
    br
    p <b>Cdigo nico de Factura Electrnica:</b> El cdigo nico de factura electrnica para las facturas electrnicas, corresponde a un valor alfanumrico, obtenido a partir de la aplicacin de un procedimiento que utiliza datos de la factura, que adicionalmente incluye la clave de contenido tcnico de control generada y entregada por la DIAN.
    br
    p El Cdigo nico de Factura Electrnica deber ser incluido como un campo ms dentro de la factura electrnica. Este cdigo deber visualizarse en la representacin grfica de las facturas electrnicas y en los cdigos bidimensionales QR definidos para tal fin.
    br
