        .h5 Recordemos que una factura al contado se registra:
        p Con un debito en la caja y un crdito en la cuenta comercio al por mayor y por menor (ingresos ordinarios), cuando es gravada la mercanca se debe reconocer la cuenta impuestos por pagar, acreditando.      

