    hr.mt-5
    h6.mt-5 Ttulos valores
    p.mt-3 El Cdigo de Comercio define los ttulos valores como:
    
      .row.justify-content-center.align-items-center.mt-5
          .col
            .bloque-texto-d.color-primario.p-4
              .bloque-texto-d__texto.mb-1
                i.fas.fa-quote-left
                span.h5 Documentos necesarios para legitimar el ejercicio del derecho literal y autnomo que en ellos se incorpora. Pueden ser de contenido crediticio, corporativos o de participacin y de tradicin o representativos de mercancas.
                i.fas.fa-quote-right
              .bloque-texto-d__autor 
                .h6.mb-0  (Secretara del Senado, 2020. Art. 619)

            p.mt-5 El Cdigo del Comercio establece (Secretara del Senado, 2020. Art. 621):
            br
            p Adems de lo dispuesto para cada ttulo-valor en particular, los ttulos-valores debern llenar los requisitos siguientes:

                ol.lista-ol--cuadro.mt-5
                  li 
                    .lista-ol--cuadro__vineta
                      span 1
                    |   La mencin del derecho que en el ttulo se incorpora
                  li 
                    .lista-ol--cuadro__vineta
                      span 2
                    |   La firma de quien lo crea.
          .col-auto 
            img.img-a.my-3(src="@/assets/curso/images/tema1/img_20.svg" alt="", style="width:350px; display:inline-block; margin:auto 5rem;").img-fluid

      p.mt-3 La firma podr sustituirse, bajo la responsabilidad del creador del ttulo, por un signo o contrasea que puede ser mecnicamente impuesto. Si no se menciona el lugar de cumplimiento o ejercicio del derecho, lo ser el del domicilio del creador del ttulo; y si tuviere varios, entre ellos podr elegir el tenedor, quien tendr igualmente derecho de eleccin si el ttulo seala varios lugares de cumplimiento o de ejercicio. Sin embargo, cuando el ttulo sea representativo de mercaderas, tambin podr ejercerse la accin derivada del mismo en el lugar en que estas deban ser entregadas. Si no se menciona la fecha y el lugar de creacin del ttulo, se tendrn como tales la fecha y el lugar de su entrega.

      h6.mt-3 Caractersticas:

      p.mt-3 Segn el Cdigo del Comercio (Secretara del Senado, 2020. Art. 622):
      br
      p Si en el ttulo se dejan espacios en blanco, cualquier tenedor legtimo podr llenarlos, conforme a las instrucciones del suscriptor que los haya dejado, antes de presentar el ttulo para el ejercicio del derecho que en l se incorpora.
      br
      p Una firma puesta sobre un papel en blanco, entregado por el firmante para convertirlo en un ttulo-valor, dar al tenedor el derecho de llenarlo. Para que el ttulo, una vez completado, pueda hacerse valer contra cualquiera de los que en l han intervenido antes de completarse, deber ser llenado estrictamente de acuerdo con la autorizacin dada para ello.
      br
      p Si un ttulo de esta clase es negociado, despus de llenado, a favor de un tenedor de buena fe exenta de culpa, ser vlido y efectivo para dicho tenedor y este podr hacerlo valer como si se hubiera llenado de acuerdo con las autorizaciones dadas.

      p.mt-3 <b>Clases</b>

      p.mt-3 Segn el Cdigo Comercio, se encuentran ttulos: nominativos, a la orden y al portador.

      .row.mt-5
